// src/details/File1.js
import { useState, useEffect } from 'react';
import { Tab, Tabs, Row, Col } from 'react-bootstrap';
import DataTable from 'react-data-table-component';
import Papa from 'papaparse';
import { countOccurrences, prepareChartData } from "../utils";
import BarChart from '../components/BarChart';

const File1 = () => {
    const [key, setKey] = useState('detail');
    const [bulkMetadata, setBulkMetadata] = useState([]);
    const [level2Values, setLevel2Values] = useState([]);

    // const [chartData, setChartData] = useState(null);

    /* const handleDataLoaded = ({data}) => {
        const counts = countOccurrences(data);
        const chartData = prepareChartData(counts);
        setChartData(chartData);
    }; */

    useEffect(() => {
        fetch('/Covid19_datastructure/data/bulk_metadata.csv')
            .then((response) => response.text())
            .then((text) => {
                Papa.parse(text, {
                    header: true,
                    skipEmptyLines: true,
                    complete: (results) => {
                        setBulkMetadata(results.data);
                        // const counts = countOccurrences(results.data);
                        // const chartData = prepareChartData(counts);
                        // setChartData(chartData);
                        const level2Values = results.data.map(row => row["level2"]);
                        const level2ValuesSet = new Set(level2Values);
                        setLevel2Values(Array.from(level2ValuesSet));
                    },
                });
            });
    }, []);

    console.log(bulkMetadata)

    const columns = bulkMetadata.length > 0 ? Object.keys(bulkMetadata[0]).map(header => ({
        name: header,
        selector: row => row[header],
        sortable: true,
    })) : [];

    const columnNames = bulkMetadata.length > 0 ? Object.keys(bulkMetadata[0]).map(header => header): []
    console.log("Bulk metadata colnames", columnNames)

    return (
        <div>
            <h2>Pseudobulk</h2>
            <hr />
            <h4>Descriptions</h4>
            <p>Detailed information about Pseudobulk.</p>
            <br />
            <h4>Data Structure</h4>
            
            <h5>Features</h5>
            <h4>Data Distribution</h4>

            <h4>File information</h4>
            <Tabs
                defaultActiveKey="detail"
                id="file-info"
                className="mb-3"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                justify
                >
                <Tab eventKey="detail" title="Detail">
                    {bulkMetadata.length > 0 ? (
                        <DataTable
                            columns={columns}
                            data={bulkMetadata}
                            pagination
                            paginationPerPage={5}
                            paginationRowsPerPageOptions={[5, 10, 15, 20]}
                        />
                    ) : (
                        <div>Loading...</div>
                    )}
                </Tab>
                <Tab eventKey="level2" title="Level 2">
                    <ul>
                        {level2Values.map((value, index) => (
                            <li key={index}>{value}</li>
                        ))}
                    </ul>
                </Tab>
                {/* <Tab eventKey="column" title="Column">
                    
                    {columnNames.map((colname) => (
                        <>
                            <h5># {colname}</h5>
                            <Row>
                                <Col>
                                     {chartData && <BarChart chartData={chartData} />}
                                </Col>
                                <Col>
                                    Statistics
                                </Col>
                            </Row>
                            <hr />
                        </>
                    ))}
                </Tab> */}
            </Tabs>

        </div>
    );
};

export default File1;
